<template>
  <div class="gallery">
    <div class="gallery__main">
      <div v-show="slide === index" v-for="(item,index) in gallery" :key="index" class="gallery__background">
        <img :src="require('@/assets/img/' + item.img)" alt="">
        <div @click="prev" class="gallery__left">
          <svg class="arrow-left-3" viewBox="0 0 5 9">
            <path d="M0.419,9.000 L0.003,8.606 L4.164,4.500 L0.003,0.394 L0.419,0.000 L4.997,4.500 L0.419,9.000 Z"/>
          </svg>
        </div>
        <div @click="nextSlide" class="gallery__right">
          <svg class="arrow-right-3" viewBox="0 0 5 9">
            <path d="M0.419,9.000 L0.003,8.606 L4.164,4.500 L0.003,0.394 L0.419,0.000 L4.997,4.500 L0.419,9.000 Z"/>
          </svg>
        </div>
        <div class="gallery__title">
          <h3>{{ item.title }}</h3>
        </div>
      </div>
    </div>
    <div class="gallery__container">
      <div @click="chooseSlide(index)" v-for="(item,index) in gallery" :key="index" class="gallery__item" :class="{ isActive: slide === index }">
        <img :src="require('@/assets/img/' + item.img)" alt="">
      </div>
    </div>
  </div>
</template>
1
<script>

export default {
  props: {
    gallery: Array
  },
  data() {
    return {
      slide:0
    }
  },
  methods: {
    nextSlide() {
      this.slide < this.gallery.length - 1? this.slide++ : this.slide = 0
    },
    prev () {
      this.slide !== 0 ? this.slide-- : this.slide = 0
    },
    chooseSlide (index) {
      this.slide = index
    }
  }

}

</script>

<style lang="scss" scoped>
.gallery {
  width: 100%;
  max-width: 1000px;

  display: flex;
  flex-direction: column;
  align-items: center;

  &__main {
    width: 100%;
    max-width: 1000px;
    min-height: 400px;

    position: relative;

    display: flex;

    overflow: hidden;
  }

  &__background {
    width: 100%;
    min-width: 1000px;

    height: 100%;
    position: absolute;

    -webkit-user-select: none;
    /* user-select -- это нестандартное свойство */

    -moz-user-select: none;
    /* поэтому нужны префиксы */

    -ms-user-select: none;
  }

  &__background img {
    width: 100%;
    height: 100%;
    max-height: 400px;
  }

  &__left {
    width: 40px;
    height: 40px;

    background: green;
    opacity: .5;

    position: absolute;
    left: 5%;
    top: calc(50% - 20px);

    transition: .5s;

  }
  &__left:hover {
    opacity: 1;
  }

  &__right  {
    width: 40px;
    height: 40px;

    background: green;
    opacity: .5;

    position: absolute;
    right: 5%;
    top: calc(50% - 20px);

    transition: .5s;
  }
  &__right:hover {
    opacity: 1;
  }
  &__title {
    padding: 5px 10px;

    background: green;

    color: #fff;

    position: absolute;
    left: 5%;
    bottom: 5%;
  }
  &__title h3 {
    margin: 0;
  }
  &__container {
    width: 100%;
    max-width: 1000px;


    display: flex;
    justify-content: center;
  }
  &__item {
    width: 100px;
    height: 100px;

    margin: 5px;

    cursor: pointer;

  }
  &__item img {
    width: 100%;
    height: 100%;
  }

}

.arrow-left-3,
.arrow-right-3 {
  margin: 10px 10px;
  width: 20px;
  height: 20px;
  cursor: pointer;

}

.arrow-left-3 path,
.arrow-right-3 path,
{
  fill: #fff;
  transition: fill 0.5s ease-out;
}


.arrow-left-3 {
  transform: rotate(180deg);
}
.isActive {
  border: 2px solid green;
}

</style>
