<template>
  <div class="slider">
    <div class="slider__controller">
      <h2 class="slider__title">{{titleName}}</h2>
      <div class="slider__points">
        <div class="slider__point"></div>
      </div>
      <div class="slider__arrows">
        <div @click="prevSlide"  class="slider__left">
          <svg class="arrow-left-3" viewBox="0 0 5 9">
            <path d="M0.419,9.000 L0.003,8.606 L4.164,4.500 L0.003,0.394 L0.419,0.000 L4.997,4.500 L0.419,9.000 Z"/>
          </svg>
        </div>
        <div @click="nextSlide" class="slider__right">
          <svg class="arrow-right-3" viewBox="0 0 5 9">
            <path d="M0.419,9.000 L0.003,8.606 L4.164,4.500 L0.003,0.394 L0.419,0.000 L4.997,4.500 L0.419,9.000 Z"/>
          </svg>
        </div>
      </div>
    </div>
    <div class="main__border">
      <div :style="{marginLeft:`${currentSlide}px`}" class="slider__container">
        <Slide v-for="(slide,index) in slider"
               :key="index"
               :slide="slide"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Slide from "@/components/slide"

export default {
  props: {
    slider: Array,
    titleName:String
  },
  components: {
    Slide,
  },
  data() {
    return {
      currentSlide: 0,

    }
  },
  methods:{
    nextSlide () {
      const maxWidth = this.slider.length*150 - 600;
      this.currentSlide > -maxWidth?this.currentSlide-=150:this.currentSlide = 0;
    },
    prevSlide () {
      const maxWidth = this.slider.length*150 - 600;
      this.currentSlide !== 0  ?this.currentSlide+=150:this.currentSlide = 0
    }


  },
  mounted() {

  }


}
</script>

<style lang="scss" scoped>
.slider {
  width: 80%;
  max-width: 1500px;
  margin: 0 10%;


  position: relative;

  overflow: hidden;

  &__controller {
    width: 100%;
    min-height: 100px;

    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__title {
    width: max-content;
    border-bottom: 1px solid #000;
  }

  &__points {
    display: none;
  }

  &__point {
    width: 10px;
    height: 10px;
    margin-right: 10%;
    background: #ccc;
    border-radius: 50%;
  }

  &__arrows {
    width: max-content;
    height: max-content;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #000000;

    position: absolute;
    right: 0;

  }

  &__left {
    display: flex;
    align-items: center;
  }

  &__right {
    display: flex;
    align-items: center;
  }
  &__border {
    width: 100%;
  }

  &__container {
    margin-top: 40px;

    display: flex;
    align-items: center;

    transition: .4s;


  }


}

.arrow-left-3,
.arrow-right-3 {
  margin: 10px 10px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.arrow-left-3 path,
.arrow-right-3 path,
{
  fill: #337AB7;
  transition: fill 0.5s ease-out;
}

svg:hover path {
  fill: #000000;
}

.arrow-left-3 {
  transform: rotate(180deg);
}

</style>
